.Dung_NA_Category{
    position: absolute;
    display: flex;
    flex-direction: row;
    background-color: white;
    height: 85%;
    width: 79%;

    margin-left: 20%;
    margin-top: 6%;
    border-radius: 30px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.Dung_NA_ListCategories{
    position: relative;
    display: flex;
    flex-direction: column;
    width: 40%;
    height: 100%;
    margin: 0%;
}
.Dung_NA_TTListCate{
    display: flex;
    position: relative;
    font-family: Inter;
    font-style: normal;
    font-weight: bold;
    font-size: 1.5vw;
    color: #22242C;
    margin-top: 5%;
    margin-left: 10%;
}
.Dung_NA_TTListCatee{
    position: relative;
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 0.65vw;
    /* Gray */
    
    color: #8E8EA1;
    margin-left: 15%;
    margin-top: 5%;
    margin-bottom: 5%;
}
.Dung_NA_OneOfCategories{
    display: flex;
    flex-direction: row;
    border-radius: 10px;
    width: 70%;
    height: 10%;
    align-self: center;
    margin-bottom: 2%;
    border: 1px solid rgb(102, 102, 240);
    cursor: pointer;
}
.Dung_NA_OneOfCategories:hover{
    background-color: rgb(102, 102, 240);
    color: white;
}
.Dung_NA_ImgCateBike{
    display: flex;
    position: relative;
    width: 20%;
    align-self: center;
    margin-left: 10%;
}
.Dung_NA_ImgCateBikee{
    width: 100%;
    height: auto;
}
.Dung_NA_ModelAndAmount{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 50%;
    margin-left: 10%;
    font-size: 12px;
}
.Dung_NA_Model{
    display: flex;
    font-family: Inter;
    font-weight: bold;

}
.Dung_NA_Amount{
    display: flex;
    font-family: Inter;
    font-weight: bold;

}
.Dung_NA_PaginationOfCate{
    display: flex;
    flex-direction: row;
    width: 50%;
    height: 10%;
    justify-content: space-evenly;
    align-self: center;
}
.Dung_NA_backPageOfCate{
    display: flex;
    background-color: #ffffff;
    position: relative;
    order: 0;
    height: 35%;
    cursor: pointer;
    align-self: center;
}
#Dung_NA_backPageiOfCate{
    height: 80%;
}
.Dung_NA_nextPageOfCate{
    display: flex;
    background-color: #ffffff;
    position: relative;
    order: 5;
    height: 35%;
    cursor: pointer;
    align-self: center;
}
#Dung_NA_nextPageiOfCate{
    height: 80%;
}
.Dung_NA_onPageOfCate{
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    align-self: center;
    position: relative;
    height: 60%;
    width: 15%;

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

    /* Inside Auto Layout */
    background-color: #6160DC   ;
    flex: none;
    font-size: 0.8vw;
    flex-grow: 0;
    box-shadow: 0px 7px 16px rgba(0, 0, 0, 0.14);
    border-radius: 50%;
}
.Dung_NA_outPageOfCate{
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    align-self: center;
    position: relative;
    height: 60%;
    width: 15%;

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

    /* Inside Auto Layout */
    background-color: #ffffff   ;
    flex: none;
    font-size: 0.8vw;
    flex-grow: 0;
    box-shadow: 0px 7px 16px rgba(0, 0, 0, 0.14);
    border-radius: 50%;
}
.Dung_NA_nbrOfOnPageOfCate{
    color: #ffffff;
}
.Dung_NA_nbrOfOutPageOfCate{
    color: black;
}
.Dung_NA_CategoryRight{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 60%;
    height: 100%;
}
.OrderBy{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    width: 100%;
    height: 8%;
}
.Dung_NA_SapXepTheott{
    display: flex;
    position: relative;
    align-self: center;
    font-family: Inter;
    font-weight: bold;
    font-size: 1vw;
    margin-right: 5%;
}
.Dung_NA_SapXepTheo{
    position: relative;
    display: flex;
    height: 40%;
    width: 20%;
    align-self: center;
    border: 1px solid black;
    margin-right: 5%;
    border-radius: 5px;
}
.Dung_NA_DetailsOfCate{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 80%;
    width: 90%;
    border: 1px solid rgb(102, 102, 240);
    margin-left: 5%;
    border-radius: 30px;
}
.Dung_NA_ImgOfThisCate{
    display: flex;
    align-self: center;
    position: relative;
    height: 40%;

}
.Dung_NA_ImgOfThisCatee{
    height: 100%;
    width: auto;
}
.Dung_NA_CateInfor{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 100%;
}
.Dung_NA_ModelAndPriceOfDetail{
    position: relative;
    display: flex;
    flex-direction: column;
    margin-left: 20%;
    font-family: Inter;
    font-weight: bold;
    font-size: 0.9vw;
}
.Dung_NA_AmountAndDebutTimeOfDetail{
    position: relative;
    display: flex;
    flex-direction: column;
    margin-left: 30%;
    font-family: Inter;
    font-weight: bold;
    font-size: 0.9vw;
}
.Dung_NA_Description{
    position: relative;
    display: flex;
    font-family: Inter;
    font-weight: bold;
    font-size: 0.9vw;
    width: 60%;
    align-self: center;
}

.Dung_NA_ButtunFunctionOfAdmin{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 7%;
}
.Dung_NA_ButtunFunctionOfAdminEdit{
    display: flex;
    position: relative;
    height: 60%;
    width: 15%;
}
.Dung_NA_ButtunFunctionOfAdminDelete{
    display: flex;
    position: relative;
    height: 60%;
    width: 15%;
}
#Dung_Newmember {
    justify-content: center;
    align-items: center;
    border-style: solid;
    border-radius: 40px;
    width: 169px;
    height: 48px;
    background-color: #6160DC;
    margin: 0 0px 0 17px;
    font-family: Poppins;
    color: #FFFFFF;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0em;
    text-decoration: none;
    margin: 43px 0px 0px 1325px;
}
