#NewmemberSideBar {
    justify-content: center;
    align-items: center;
    border-style: solid;
    border-radius: 20px;
    width: 200px;
    height: 48px;
    background-color: #6160DC;
    font-family: Poppins;
    color: #FFFFFF;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0em;
    text-decoration: none;
    margin-left: 70px;
    margin-bottom: 10px;
}